
.top5 {
  width: 100%;
  height: 0.71rem;
  background: url(../assets/images/neiye-topbg.png) no-repeat;
  font-size: 0.3rem;
  font-weight: 400;
  line-height: 0.71rem;
  color: #008dce;
  padding-left: 0.6rem;
  background-size: 100% 100%;
}
.huodong {
  width: 5.9rem;
  height: 5.13rem;
  margin: 0.2rem auto 0.7rem;
  background: #fff;
  border-radius: 7px;
  padding: 0.09rem 0 0 0.06rem;
}
.neirong-bg {
  width: 5.76rem;
  height: 2.78rem;
  background: url(../assets/images/huodong.png) no-repeat;
  background-size: 100% 100%;
}
.huodong-wenben {
  width: 4.44rem;
  height: 1.14rem;
  margin: 0.04rem 0 0.22rem 0;
  padding-left: 0.22rem;
}
.huodong-wenben li {
  width: 100%;
  height: 0.38rem;
  line-height: 0.38rem;
  color: #837f7f;
}
.baoming {
  display: block;
  width: 5.65rem;
  height: 0.72rem;
  margin: 0 auto;
  background: #1eb9f6;
  border-radius: 4px;
  color: #fff;
  line-height: 0.72rem;
  text-align: center;
  font-size: 0.29rem;
}



