
.bottom {
  width: 100%;
  height: 1.66rem;
}
.bottom-neirong {
  padding-top: 0.14rem;
  overflow: hidden;
  width: 100%;
  height: 1.06rem;
  background: url(../../assets/images/bottom.png) no-repeat;
  background-size: 100% 100%;
  position: relative;
}
.bottom-left {
  margin-left: 0.64rem;
  width: auto;
  height: 0.45rem;
  padding-left: 0.45rem;
  background: url(../../assets/images/bottom-tb.png) no-repeat;
  background-size: 100% 100%;
  float: left;
}
.bottom-left p {
  width: auto;
}
.bottom-right {
  margin-right: 0.5rem;
  width: auto;
  height: 0.45rem;
  padding-left: 0.45rem;
  background: url(../../assets/images/bottom-tb1.png) no-repeat;
  background-size: 100% 100%;
  float: right;
}
.bottom-right p {
  width: auto;
}
.bottom-neirong-1 {
  position: absolute;
  left: 50%;
  bottom: 0.14rem;
  width: 3rem;
  height: 0.2rem;
  margin-left: -1.5rem;
  padding-left: 0.85rem;
  color: #909090;
}
.bottom-neirong-1 span {
  float: left;
  margin-right: 0.21rem;
}
.zuixia {
  width: 100%;
  height: 0.61rem;
  position: relative;
  background: #fff;
}
.zuixia span {
  position: absolute;
  left: 50%;
  top: 50%;
  display: block;
  width: auto;
  height: 0.18rem;
  color: #909090;
  margin: -0.09rem 0 0 -20%;
  font-size: 0.18rem;
  padding-left: 0.05rem;
}



