
.top2 {
  width: 100%;
  height: 1.83rem;
}
.top2 img {
  width: 100%;
  height: 100%;
}
.Middle2 {
  margin: 0.48rem auto 1rem;
  width: 5.76rem;
  height: auto;
  padding-bottom: 3.62rem;
}
.Middle2 a {
  color: #3c3535;
  display: block;
  width: 5.74rem;
