  line-height: 0.86rem;
  font-size: 0.3rem;
}
.Middle2 a:hover {
  color: #fff;
  width: 5.76rem;
  height: 0.86rem;
  margin-bottom: 0.03rem;
  border-radius: 0.06rem;
  text-align: center;
  line-height: 0.86rem;
  font-size: 0.3rem;
  background: url(../../assets/images/libg.png) no-repeat;
  background-size: 100% 100%;
}



