
@charset "UTF-8";
[v-cloak] {
  display: none;
}
.app {
  width: 100%;
  height: 100%;
}
li, h1, h2, h3, h4, h5, h6, body, p, div, section, dl, dt, dd, span, ul, li, a, b, strong, header, img, footer, form, article, em, input, label {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -ms-touch-action: none;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
p {
  list-style: none;
}
img {
  border: none 0;
  vertical-align: top;
}
html {
  font-size: 625%;
  /*100%=16px,625%=100px 16X6.25*/
}
img {
  border: none;
}
body {
  max-width: 640px;
  min-width: 320px;
  margin: 0 auto;
  font-family: "微软雅黑";
  font-size: 0.18rem;
  background: #f1f2f2;
}
.clearfix {
  *zoom: 1;
}
.clearfix:after {
  height: 0;
  font: 0;
  clear: both;
  visibility: hidden;
  content: ".";
  display: block;
}



