
.Middle4 {
  padding-bottom: 2rem;
}
.top4 {
  width: 100%;
  height: 0.71rem;
  background: url(../assets/images/neiye-topbg.png) no-repeat;
  font-size: 0.3rem;
  font-weight: 400;
  line-height: 0.71rem;
  text-align: center;
  color: #008dce;
  background-size: 100% 100%;
}
.Middle5 {
  margin: 0.48rem auto 1rem;
  width: 5.76rem;
  height: auto;
}
.Middle5 li {
  width: 5.74rem;
  height: 0.86rem;
  margin-bottom: 0.03rem;
  background: #fff;
  border: 1px solid #d4d4d4;
  border-radius: 0.06rem;
  text-align: center;
  line-height: 0.86rem;
  font-size: 0.3rem;
}
.Middle5 a {
  color: #3c3535;
  display: block;
  width: 100%;
  height: 100%;
}
.Middle5 li:hover {
  width: 5.76rem;
  height: 0.86rem;
  margin-bottom: 0.03rem;
  border-radius: 0.06rem;
  text-align: center;
  line-height: 0.86rem;
  font-size: 0.3rem;
  background: url(../assets/images/libg.png) no-repeat;
  background-size: 100% 100%;
}
.Middle5 a:hover {
  color: #fff;
}



